.Button {
  cursor: pointer;
  color: var(--primary-color);
  padding: 6px 15px;
}

.classic {
  &.active {
    background: var(--secondary-color);
  }
}

.clear {
  padding: 0;
  border: 0;
  outline: none;
  background: none;
}

.outline {
  background: none;
  border: 1px solid var(--primary-color);
  color: var(--primary-color);
}

.background {
  background-color: var(--bg-color);
  color: var(--primary-color);
  border: none;
}

.backgroundInverted {
  background-color: var(--inverted-bg-color);
  color: var(--inverted-primary-color);
  border: none;
}

.square {
  padding: 0;
}

.square.size_m {
  width: var(--font-line-m);
  height: var(--font-line-m);
  font: var(--font-m);
}

.square.size_l {
  width: var(--font-line-l);
  height: var(--font-line-l);
  font: var(--font-l);
}

.square.size_xl {
  width: var(--font-line-xl);
  height: var(--font-line-xl);
  font: var(--font-xl);
}

.size_m {
  font: var(--font-m);
}

.size_l {
  font: var(--font-l);
}

.size_xl {
  font: var(--font-xl);
}
